export const CHINESE_NUMBER_LIST: string[] = [
  '零',
  '一',
  '二',
  '三',
  '四',
  '五',
  '六',
  '七',
  '八',
  '九',
];
export const CHINESE_UNIT_LIST: string[] = [
  '',
  '十',
  '百',
  '千',
  '万',
  '十',
  '百',
  '千',
  '亿',
  '十',
  '百',
  '千',
  '万',
];

export default function arabic2Chinese(num: number): string {
  let unitPos = 0;
  let zero = true;
  let strIns = '';
  let chnStr = '';
  let arabic = num;
  while (arabic > 0) {
    const v: number = arabic % 10;
    if (v === 0) {
      if (!zero) {
        zero = true;
        chnStr = CHINESE_NUMBER_LIST[v] + chnStr;
      }
    } else {
      zero = false;
      strIns = CHINESE_NUMBER_LIST[v];
      strIns += CHINESE_UNIT_LIST[unitPos];
      chnStr = strIns + chnStr;
    }
    unitPos += 1;
    arabic = Math.floor(arabic / 10);
  }
  if (num > 9 && num < 20) {
    return chnStr.slice(1);
  }
  return chnStr;
}
